@tailwind base;
@tailwind components;
@tailwind utilities;

[x-cloak]{
  @apply hidden #{!important};
}

h1 {
  @apply font-bold text-5xl mb-6;
}

h2 {
  @apply font-bold text-2xl mb-2;
}
h3{
  @apply font-bold mb-2.5;
}

a{
  @apply underline;
}
p{
  @apply text-sm mb-5 text-[#595959] dark:text-gray-200;
}

.resume{
  box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.1);
  hyphens: auto;
  
}

html,body{
  @apply max-w-full overflow-x-clip;
}


.contacts {
  @apply mb-10;
  &__item{
    @apply flex gap-3 text-sm mb-1.5 text-white;
    a{
      @apply opacity-50;
    }
    &--tel{
      a{
        @apply no-underline;
      }
    }
  }
}

.skills {
  @apply mb-10;
  &--item{

  }
  ul{
    @apply list-disc text-[#FB6D3A] pl-5 text-xs;
    li{
      @apply mb-1.5;
      
      & > *{
        @apply text-white text-sm opacity-90;
      }

    }
  }
}

.projects{
  @apply max-w-full overflow-hidden;
  &__item{
    @apply flex flex-wrap w-full;
    &> *{
      
    }
  }
  &__delimeter{
    @apply inline-flex  grow border-b border-dashed mb-1 mx-1 min-w-[10px] border-gray-300  ;
  }
  &__skills{
    @apply grow-0 font-normal dark:text-blue-400 mr-0 ml-auto ;
  }
  ol{
    @apply list-decimal font-bold pl-5;
    li{
      @apply pl-2 w-full;
    }
    a{
      @apply font-normal grow-0 dark:text-orange-300;
    }
   
  }
}

.work-experience{
  @apply mb-11;
  ul{
    @apply pl-5 list-disc text-[#595959] dark:text-gray-200 text-xs mb-4;
  }
  li{
    @apply mb-1.5;
  }
  p{
    @apply last-of-type:mb-0 text-sm;
  }

  &__period{
    @apply inline-block mb-2 text-xs text-gray-400 ;
  }
}